@include ipad-landscape {
    .edgtf-woocommerce-page.edgtf-woocommerce-order-tracking {
        .woocommerce {
            > .track_order {
                .form-row {
                    float: none;
                }

                .form-row-first,
                .form-row-last {
                    margin-right: 0;
                    width: 100%;
                }

                .form-row:not(.form-row-first):not(.form-row-last) {
                    width: 100%;
                }
            }
        }
    }
}