/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include laptop-landscape() {
    .edgtf-woocommerce-page {
        .woocommerce-cart-form {
            padding-right: 0;
            width: 100%;
        }

        .woocommerce-message {
            width: 100%;
        }

        .cart_totals {
            position: relative;
            margin-top: 50px;
            right: initial;
            top: initial;
            width: 100%;

            .woocommerce-shipping-calculator {
                width: 100%;
            }
        }
    }
}

@include ipad-portrait() {
    .edgtf-woocommerce-page {
        table.cart {
            td.actions {
                .coupon {
                    input[type="text"] {
                        width: 180px;
                        margin: 0 17px 0 0;
                        padding: 0 20px;
                    }
                }
            }
        }
    }

    .woocommerce-cart-form__cart-item .edgtf-quantity-buttons-inner {
        .edgtf-quantity-plus,
        .edgtf-quantity-minus {
            display: none !important;
        }

        .edgtf-quantity-input {
            margin: 0 !important;
            text-align: left !important;
        }
    }
}

@include phone-landscape() {
    .edgtf-woocommerce-page {
        table.cart {
            tr {
                th, td {
                    font-size: 12px;
                }

                &.cart_item {
                    td {
                        &.product-thumbnail {
                            width: auto;
                            padding-right: 0;

                            img {
                                display: none;
                            }
                        }
                    }
                }
            }

            td.actions {
                .coupon {
                    margin: 0 0 20px;

                    input[type="text"] {
                        width: 160px;
                        padding: 0 15px;
                        margin: 0 10px 20px 0;
                    }
                }

                .coupon,
                > input {
                    float: none;
                    display: block;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */