/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-landscape {
    .woocommerce-page .edgtf-content,
    div.woocommerce {
        .edgtf-quantity-buttons {
            .edgtf-quantity-input {
                font-size: 13px;
                height: 44px;
                line-height: 44px;
            }

            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                height: 44px;
                line-height: 44px;
            }
        }
    }
}

@include ipad-portrait() {
    .woocommerce-page .edgtf-content .edgtf-quantity-buttons,
    div.woocommerce .edgtf-quantity-buttons {
        border: none;

        label {
            display: none;
        }
    }
}

