/* ==========================================================================
   Product List - Carousel shortcode responsive styles - begin
   ========================================================================== */

@include ipad-landscape() {
    .edgtf-plc-holder.edgtf-simple-layout {
        .edgtf-plc-text {
            padding: 50px 50px 50px 60px;
        }

        .edgtf-plc-excerpt {
            display: none;
        }
    }
}

@include ipad-portrait() {
    .edgtf-plc-holder.edgtf-simple-layout {
        .edgtf-plc-text {
            padding: 30px 30px 30px 40px;
        }
    }
}

@include phone-landscape() {
    .edgtf-plc-holder.edgtf-simple-layout {
        .edgtf-plc,
        .edgtf-plc-inner,
        .edgtf-plc-text-wrapper {
            @include edgtfRelativeHolderLayout();

            padding: 0;
        }

        .edgtf-plc-buttons-wrapper-outer {
            left: 0;
            width: 100%;
        }

        .edgtf-plc-text {
            padding: 30px 0 0 0;
            text-align: center;
        }

        .edgtf-plc-background-holder {
            display: none;
        }
    }
}

/* ==========================================================================
   Product List - Carousel shortcode responsive styles - end
   ========================================================================== */