@include laptop-landscape-medium() {
    .edgtf-product-info-carousel .edgtf-pic-content .edgtf-pic-content-inner {
        .edgtf-pic-rating {
            margin-bottom: 11px;
        }

        .edgtf-pic-excerpt {
            margin-bottom: 22px;
        }
    }
}

@include laptop-landscape() {
    .edgtf-product-info-carousel .edgtf-pic-content .edgtf-pic-content-inner {
        padding: 65px 70px 80px calc(50% + 60px)
    }
}

@include ipad-landscape() {
    .edgtf-product-info-carousel {
        .edgtf-pic-content {
            .edgtf-pic-content-inner {
                padding: 50px 50px 65px calc(50% + 50px);
            }
        }

        .edgtf-pic-excerpt {
            display: none;
        }
    }
}

@include ipad-portrait() {
    .edgtf-product-info-carousel {
        .edgtf-pic-content {
            .edgtf-pic-content-inner {
                padding: 30px 30px 45px calc(50% + 30px);
            }
        }

        .edgtf-pic-price,
        .edgtf-pic-rating {
            display: none !important;
        }
    }
}

@include phone-landscape() {
    .edgtf-product-info-carousel {
        .edgtf-product-info-carousel-images {
            @include edgtfRelativeHolderLayout();

            left: 0;
            padding: 0;
            top: 0;

            @include edgtfTransform(translateY(0));
        }

        .edgtf-owl-slider .owl-nav {
            display: none;
        }

        .edgtf-pic-content {
            padding: 0 0 15px 0;

            .edgtf-pic-content-inner {
                background: transparent;
                border: none;
                padding: 30px 0 0 0;
            }
        }
    }
}